.NavigationItem {
    margin: 20px;
    
    /* border-box: padding and border included in element */
    box-sizing: border-box; 
    display: block;
}

.NavigationItem a {
    display: flex;
    align-items: center;
    justify-content: left;
    height: 100%;
    width: 100%;
    text-decoration: none;
    color: white;
    font-weight: bold;
    font-size: x-large;
}

.NavigationItem a:hover {
    color: white;
}



